html {
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1, h2, h3, p {
  margin: 0;
  padding: 0;
}

.lazyload, .lazyloading {
  opacity: 0;
  transition: all 1s;
  transform: translateX(-100%);
}

.lazyloaded {
  opacity: 1;
  transition: transform 1s, opacity 1s;
  transform: translateY(0);
}

.container {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 10px;
  padding-right: 10px;
}

body {
  background-color: rgba(38, 36, 36, .918);
  background-image: url("background-fon-2.6a73f418.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  margin: 0;
  font-family: Aboreto, Arial, Helvetica, sans-serif;
  font-size: 14px;
}

body.stop-fon {
  overflow: hidden;
}

body.white-theme {
  background-image: url("background-white-theme.e83040f9.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.page-header {
  width: 100%;
  z-index: 10;
  background-color: rgba(11, 12, 12, .7);
  margin-bottom: 5px;
  padding-left: 20px;
  padding-right: 20px;
  position: fixed;
  top: 0;
  right: 0;
}

.page-header .container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  color: gold;
  text-shadow: 0 0 10px #06e6f6;
  padding-top: 10px;
  padding-bottom: 10px;
  font-family: Aboreto;
  font-size: 27px;
  font-weight: bold;
}

.logo:focus {
  outline: none;
  transform: scale(1.1);
}

body.white-theme .logo {
  text-shadow: 0 0 10px #f20a0a;
}

.search-form {
  justify-content: center;
  align-items: center;
  column-gap: 10px;
  display: flex;
}

.input-search {
  width: 200px;
  height: 30px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 5px;
  outline: 1px solid gold;
  padding-left: 10px;
  padding-right: 10px;
  font-family: Aboreto;
  box-shadow: 0 0 10px 10px rgba(6, 230, 246, .7), inset 0 0 30px 10px rgba(6, 230, 246, .7);
}

.input-search:focus {
  transform: scale(1.1);
}

body.white-theme .input-search {
  box-shadow: 0 0 10px 10px rgba(242, 10, 10, .7);
}

.input-search::-ms-input-placeholder {
  color: gold;
}

.input-search::placeholder {
  color: gold;
}

.search-button {
  cursor: pointer;
  width: 90px;
  height: 30px;
  color: gold;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 5px;
  outline: 1px solid gold;
  padding: 5px;
  font-family: Aboreto;
  box-shadow: 0 0 10px 10px rgba(6, 230, 246, .7), inset 0 0 30px 10px rgba(6, 230, 246, .7);
}

.search-button:focus {
  transform: scale(1.1);
}

body.white-theme .search-button {
  box-shadow: 0 0 10px 10px rgba(242, 10, 10, .7);
}

.genre-list {
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 5px;
  margin-bottom: 40px;
  padding: 10px;
  font-family: Aboreto;
  list-style: none;
  display: flex;
}

.genre-item {
  cursor: pointer;
  width: 190px;
  color: gold;
  text-transform: uppercase;
  background-color: rgba(10, 10, 10, .624);
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  padding: 5px;
  font-size: 21px;
  transition: transform .3s;
  display: flex;
  box-shadow: 0 0 10px 10px rgba(6, 230, 246, .7);
}

.genre-item:hover {
  transform: scale(1.04);
}

body.white-theme .genre-item {
  box-shadow: 0 0 10px 10px rgba(242, 10, 10, .7);
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  display: flex;
}

.movie-card__item {
  max-width: 200px;
  background-color: rgba(255, 227, 194, .3);
  border-radius: 10px;
  transition: transform .3s, box-shadow .3s, filter .3s;
}

.movie-card__item:hover {
  filter: saturate(165%);
}

body.white-theme .movie-card__item {
  background-color: rgba(14, 14, 13, .7);
}

body.white-theme .movie-card__item:hover {
  box-shadow: 0 0 10px 10px rgba(242, 10, 10, .7);
}

.poster-wrapper {
  overflow: hidden;
}

.movie-card__poster {
  cursor: pointer;
  border-radius: 10px 10px 0 0;
}

.movie-card__poster:hover {
  transform: scale(1.1);
}

.card-info {
  pointer-events: none;
  flex-direction: column;
  padding: 10px;
  display: flex;
}

.movie-info-title {
  text-overflow: ellipsis;
  width: 160px;
  height: 1.2em;
  white-space: nowrap;
  color: gold;
  text-align: center;
  margin-bottom: 5px;
  font-size: 14px;
  overflow: hidden;
}

.info-wrapper {
  justify-content: space-between;
  align-items: baseline;
  margin-top: auto;
  display: flex;
}

.movie-icon-decor {
  margin-bottom: 5px;
}

.info-item-year, .info-item-language {
  color: gold;
  margin-bottom: 5px;
  font-size: 12px;
}

.rating-icon {
  color: #daa520;
}

.info-item-rating {
  color: #ccc8c8;
}

.modal {
  max-width: 700px;
  background-color: bisque;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  display: flex;
  box-shadow: 0 0 10px 10px rgba(6, 230, 246, .7);
}

body.white-theme .modal {
  box-shadow: 0 0 10px 10px rgba(242, 10, 10, .7);
}

.img-container {
  max-width: 50%;
}

.modal-img {
  width: 100%;
  border-radius: 10px 0 0 10px;
}

.modal-wrapper {
  display: flex;
}

.modal-info {
  width: 50%;
  flex-direction: column;
  row-gap: 20px;
  padding: 10px;
  display: flex;
}

.modal-title {
  font-size: 18px;
}

.swiper {
  border-radius: 10px;
  margin-bottom: 10px;
  box-shadow: 0 0 10px 10px rgba(6, 230, 246, .7);
}

body.white-theme .swiper {
  box-shadow: 0 0 10px 10px rgba(242, 10, 10, .7);
}

.swiper-wrapper {
  column-gap: 10px;
  padding: 10px;
}

.swiper-slide {
  cursor: pointer;
  max-width: 180px;
  border-radius: 10px;
  padding: 10px;
  transition: transform .3s, box-shadow .3s, filter .3s;
  box-shadow: 0 0 10px 10px rgba(6, 230, 246, .7);
}

.swiper-slide:hover {
  filter: saturate(165%);
  transform: scale(1.02);
}

body.white-theme .swiper-slide {
  box-shadow: 0 0 10px 10px rgba(242, 10, 10, .7);
}

.rated-title {
  color: gold;
  text-shadow: 0 0 20px #c9a22d;
  justify-content: center;
  align-items: center;
  margin-top: 60px;
  margin-bottom: 10px;
  font-family: Aboreto;
  font-size: 42px;
  font-weight: 700;
  display: flex;
}

body.white-theme .rated-title {
  text-shadow: 0 0 10px #f20a0a;
}

.rated-img {
  width: 100%;
  border-radius: 10px;
  margin-bottom: 5px;
}

.rated-info-box {
  padding: 5px;
}

body.white-theme .rated-info-box {
  background-color: rgba(14, 14, 13, .6);
}

.rated-name {
  color: gold;
  text-overflow: ellipsis;
  width: 150px;
  height: 1.2em;
  white-space: nowrap;
  text-align: center;
  font-family: Aboreto;
  overflow: hidden;
}

.swiper-button-prev, .swiper-button-next {
  width: 37px;
  height: 35px;
  cursor: pointer;
}

.ganre-populary {
  position: relative;
}

.swiper-button-next {
  z-index: 10;
  position: absolute;
  top: 0;
  left: -10px;
}

@font-face {
  font-family: Anton;
  src: url("Anton-Regular.c9331b93.woff2");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: Oswald;
  src: url("Oswald-Regular.d9954873.woff2");
  font-weight: normal;
  font-style: normal;
}

.button-sroll-up {
  position: relative;
}

.btn-scroll {
  width: 30px;
  height: 30px;
  opacity: .7;
  z-index: 200;
  transition: all .3;
  background-color: red;
  border-radius: 30px;
  font-size: 0;
  display: none;
  position: fixed;
  bottom: 10px;
  right: 10px;
  box-shadow: 0 0 10px 10px rgba(201, 162, 45, .7), inset 0 0 30px 10px rgba(201, 162, 45, .7);
}

body.white-theme .btn-scroll {
  box-shadow: 0 0 10px 10px rgba(242, 10, 10, .7);
}

.btn-scroll.shown {
  display: block;
}

.btn-scroll:before {
  content: "";
  width: 20px;
  height: 20px;
  background-image: url("pngwing.com.3054d48a.png");
  background-repeat: no-repeat;
  background-size: cover;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.btn-scroll:hover {
  transform: scale(1.1);
  box-shadow: 0 0 10px 10px rgba(6, 230, 246, .7), inset 0 0 30px 10px rgba(6, 230, 246, .7);
}

.btn-scroll:active {
  transform: scale(1.2);
}

.theme-btn {
  width: 30px;
  background: #2a2c2d;
  background-color: inherit;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.theme-btn button {
  color: #000;
  background: #0060df;
  border: 0;
  border-radius: 5px;
  padding: 10px 20px;
}

.switch {
  width: 45px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.switch:focus {
  outline: none;
  transform: scale(1.1);
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background-color: rgba(204, 204, 204, .3);
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 25px;
  width: 25px;
  background: #fff url("https://i.ibb.co/FxzBYR9/night.png") center / 16px no-repeat;
  margin: auto 0;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  box-shadow: 0 0 15px rgba(32, 32, 32, .24);
}

input:checked + .slider {
  background-color: rgba(204, 204, 204, .3);
}

input:checked + .slider:before {
  background: #fff url("https://i.ibb.co/7JfqXxB/sunny.png") center / 20px no-repeat;
  transform: translateX(20px);
}

.slider.round {
  border-radius: 15px;
}

.slider.round:before {
  border-radius: 50%;
}

.skiptranslate {
  height: 0;
  display: none;
}

.language__img {
  cursor: pointer;
}

.cont-lang {
  position: relative;
}

.wrapper-language {
  z-index: 50;
  cursor: pointer;
  text-align: center;
  text-transform: uppercase;
  width: 150px;
  color: gold;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 10px;
  margin-left: 50px;
  padding: 5px;
  font-size: 12px;
  font-weight: bold;
  transition: transform .3s;
  position: relative;
  box-shadow: 0 0 10px 10px rgba(6, 230, 246, .7);
}

.wrapper-language:hover, .wrapper-language:focus {
  outline: none;
  transform: scale(1.1);
}

.language {
  width: 100%;
  pointer-events: none;
  color: gold;
  text-shadow: 0 0 1px #06e6f6;
  background-color: rgba(13, 13, 13, .3);
  border-radius: 10px;
  padding: 5px;
  transition: all .4s;
  position: absolute;
  top: 100%;
  left: 0;
  transform: perspective(600px)translate(0, -50%)rotateX(45deg)scale(0);
}

.language.shows {
  pointer-events: painted;
  transform: perspective(600px)translate(0)rotateX(0deg)scale(1);
}

.item-language {
  text-transform: uppercase;
  cursor: pointer;
  background-color: rgba(9, 137, 235, .7);
  border-radius: 10px;
  justify-content: space-between;
  align-items: center;
  gap: 15px;
  padding: 5px;
  font-size: 12px;
  font-weight: bold;
  transition: transform .3s;
  display: flex;
}

.item-language:hover {
  transform: scale(1.02);
}

.item-language:not(:last-child) {
  margin-bottom: 5px;
}

.back-drop-modal {
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 25;
  background-color: rgba(0, 0, 0, .4);
  background-image: url("background-fon.48f0c9b5.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: hidden;
}

.back-drop-modal.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.modal-window {
  max-width: 528px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.title-modal-sign {
  text-align: center;
  color: #ffb700;
  text-shadow: 0 0 5px #1f3514;
  margin-bottom: 25px;
  font-size: 36px;
}

.btn-wrapper {
  gap: 20px;
  display: flex;
}

.btn-modal-sign, .btn-modal-sign-git {
  cursor: pointer;
  width: 210px;
  height: 30px;
  color: gold;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 5px;
  outline: 1px solid gold;
  justify-content: center;
  align-items: center;
  padding: 5px;
  font-family: Aboreto;
  transition: transform .4s;
  display: flex;
  position: relative;
  box-shadow: 0 0 10px 10px rgba(201, 162, 45, .9), inset 0 0 30px 10px rgba(201, 162, 45, .9);
}

.btn-modal-sign:hover, .btn-modal-sign:focus, .btn-modal-sign-git:hover, .btn-modal-sign-git:focus {
  transform: scale(1.1);
}

.btn-modal-sign:before, .btn-modal-sign-git:before {
  content: " G";
  color: red;
  font-size: 20px;
  display: block;
  position: absolute;
  right: 10px;
}

.btn-modal-sign-git:before {
  content: "";
  width: 20px;
  height: 20px;
  background-image: url("icons8-github.cb99f071.svg");
  background-position: center;
  background-size: cover;
  display: block;
}

.btn-log-out {
  cursor: pointer;
  max-width: 80px;
  height: 30px;
  color: gold;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 5px;
  outline: 1px solid gold;
  justify-content: center;
  align-items: center;
  padding: 5px;
  font-family: Aboreto;
  font-size: 12px;
  transition: transform .4s;
  display: flex;
  position: relative;
  box-shadow: 0 0 10px 10px rgba(6, 230, 246, .7);
}

.btn-log-out:hover, .btn-log-out:focus {
  transform: scale(1.1);
}

.carousel {
  box-sizing: border-box;
  position: relative;
}

.carousel *, .carousel :before, .carousel :after {
  box-sizing: inherit;
}

.carousel.is-draggable {
  cursor: move;
  cursor: grab;
}

.carousel.is-dragging {
  cursor: move;
  cursor: grabbing;
}

.carousel__viewport {
  max-width: 100%;
  max-height: 100%;
  position: relative;
  overflow: hidden;
}

.carousel__track {
  display: flex;
}

.carousel__slide {
  width: var(--carousel-slide-width, 60%);
  max-width: 100%;
  overscroll-behavior: contain;
  flex: none;
  padding: 1rem;
  position: relative;
  overflow-x: hidden;
  overflow-y: auto;
}

.has-dots {
  margin-bottom: calc(.5rem + 22px);
}

.carousel__dots {
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  justify-content: center;
  margin: 0 auto;
  padding: 0;
  list-style: none;
  display: flex;
  position: absolute;
  top: calc(100% + .5rem);
  left: 0;
  right: 0;
}

.carousel__dots .carousel__dot {
  width: 22px;
  height: 22px;
  cursor: pointer;
  margin: 0;
  padding: 0;
  display: block;
  position: relative;
}

.carousel__dots .carousel__dot:after {
  content: "";
  width: 8px;
  height: 8px;
  opacity: .25;
  background-color: currentColor;
  border-radius: 50%;
  transition: opacity .15s ease-in-out;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.carousel__dots .carousel__dot.is-selected:after {
  opacity: 1;
}

.carousel__button {
  width: var(--carousel-button-width, 48px);
  height: var(--carousel-button-height, 48px);
  pointer-events: all;
  cursor: pointer;
  color: var(--carousel-button-color, currentColor);
  background: var(--carousel-button-bg, transparent);
  border-radius: var(--carousel-button-border-radius, 50%);
  box-shadow: var(--carousel-button-shadow, none);
  border: 0;
  justify-content: center;
  align-items: center;
  padding: 0;
  transition: opacity .15s;
  display: flex;
}

.carousel__button.is-prev, .carousel__button.is-next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.carousel__button.is-prev {
  left: 10px;
}

.carousel__button.is-next {
  right: 10px;
}

.carousel__button[disabled] {
  cursor: default;
  opacity: .3;
}

.carousel__button svg {
  width: var(--carousel-button-svg-width, 50%);
  height: var(--carousel-button-svg-height, 50%);
  fill: none;
  stroke: currentColor;
  stroke-width: var(--carousel-button-svg-stroke-width, 1.5);
  stroke-linejoin: bevel;
  stroke-linecap: round;
  filter: var(--carousel-button-svg-filter, none);
  pointer-events: none;
}

html.with-fancybox {
  scroll-behavior: auto;
}

body.compensate-for-scrollbar {
  touch-action: none;
  overflow: hidden !important;
}

.fancybox__container {
  direction: ltr;
  padding: env(safe-area-inset-top, 0px) env(safe-area-inset-right, 0px) env(safe-area-inset-bottom, 0px) env(safe-area-inset-left, 0px);
  box-sizing: border-box;
  color: var(--fancybox-color, #fff);
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  z-index: 1050;
  transform-origin: 0 0;
  --carousel-button-width: 48px;
  --carousel-button-height: 48px;
  --carousel-button-svg-width: 24px;
  --carousel-button-svg-height: 24px;
  --carousel-button-svg-stroke-width: 2.5;
  --carousel-button-svg-filter: drop-shadow(1px 1px 1px rgba(0, 0, 0, .4));
  outline: none;
  flex-direction: column;
  margin: 0;
  display: flex;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}

.fancybox__container *, .fancybox__container :before, .fancybox__container :after {
  box-sizing: inherit;
}

.fancybox__container :focus {
  outline: none;
}

body:not(.is-using-mouse) .fancybox__container :focus {
  box-shadow: 0 0 0 1px #fff, 0 0 0 2px var(--fancybox-accent-color, rgba(1, 210, 232, .94));
}

@media (min-width: 1024px) {
  .fancybox__container {
    --carousel-button-width: 48px;
    --carousel-button-height: 48px;
    --carousel-button-svg-width: 27px;
    --carousel-button-svg-height: 27px;
  }
}

.fancybox__backdrop {
  z-index: -1;
  background: var(--fancybox-bg, rgba(24, 24, 27, .92));
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.fancybox__carousel {
  min-height: 0;
  height: 100%;
  z-index: 10;
  flex: auto;
  position: relative;
}

.fancybox__carousel.has-dots {
  margin-bottom: calc(.5rem + 22px);
}

.fancybox__viewport {
  width: 100%;
  height: 100%;
  cursor: default;
  position: relative;
  overflow: visible;
}

.fancybox__track {
  height: 100%;
  display: flex;
}

.fancybox__slide {
  width: 100%;
  max-width: 100%;
  overscroll-behavior: contain;
  --carousel-button-width: 36px;
  --carousel-button-height: 36px;
  --carousel-button-svg-width: 22px;
  --carousel-button-svg-height: 22px;
  outline: 0;
  flex-direction: column;
  flex: none;
  margin: 0;
  padding: 48px 8px 8px;
  display: flex;
  position: relative;
  overflow: auto;
}

.fancybox__slide:before, .fancybox__slide:after {
  content: "";
  flex: 0 0;
  margin: auto;
}

@media (min-width: 1024px) {
  .fancybox__slide {
    padding: 64px 100px;
  }
}

.fancybox__content {
  margin: 0 env(safe-area-inset-right, 0px) 0 env(safe-area-inset-left, 0px);
  color: var(--fancybox-content-color, #374151);
  background: var(--fancybox-content-bg, #fff);
  z-index: 20;
  flex-direction: column;
  align-self: center;
  padding: 36px;
  display: flex;
  position: relative;
}

.fancybox__content :focus:not(.carousel__button.is-close) {
  box-shadow: none;
